$cor-primaria: #39b1ff;
$cor-secundaria: #226591;
$preto: #1b1b1b;
$branco: #ffffff;
$cinza: #a1a1a1;

body {
  margin: 0;
  padding: 0;
  background: $preto;
  color: $branco;
}

.container {
  max-width: 1000px;
  align-content: center;
  align-items: center;
  margin: 0 auto;
}

.navbar {
  background: $cor-primaria;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .navbar-brand {
    color: $branco;
    font-weight: 900;
    font-size: 35px;
    padding: 10px;
    margin: 15px;
    text-decoration: none;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
  }
  .nav-item {
    list-style: none;
    margin: 15px;
  }
  .nav-link {
    color: $branco;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 20px;
    font-family: 'Raleway', sans-serif;
    font-weight: 500;
    padding: 15px;
  }
}

.text-center {
  text-align: center;
}

.title {
  font-size: 32px;
}

input, label, form {
  color: white;
  background: blue;
  font-family: 'Epilogue', sans-serif;
}